<template>
  <div class="container" id="api-post">
    <MDBModal
      tabindex="-1"
      style="position: static; display: block; height: auto"
      :modelValue="isActive"
      removeBackdrop
      centered
      class="color-info"
      bgSrc="https://shinigamidevelopment.000webhostapp.com/img/bg.svg"
    >
      <MDBModalHeader :close="false">
        <MDBModalTitle class="title" style="color: white"
          ><box-icon
            name="bullseye"
            type="solid"
            color="white"
            size="24px"
          ></box-icon>
          Welcome to <br />
          <span class="subtitle has-text-centered" style="color: #6f0000">
            ROCK PAPER SHOTGUN
          </span></MDBModalTitle
        >
      </MDBModalHeader>
      <MDBModalBody>
        <p style="color: white" class="subtitle">
          This is a traditional game of rock paper scissors, you play against
          the AI and rack up points to win, first to 5 points is the champion.
        </p>
      </MDBModalBody>
      <MDBModalFooter>
        <MDBBtn color="info" @click="startGame()"> <b>Start Game</b> </MDBBtn>
      </MDBModalFooter>
    </MDBModal>
    <MDBContainer v-if="isGameActive" class="pt-5">
      <div v-if="playerScore < 5 || computerScore < 5">
        <div class="columns">
          <div class="column">
            <p>Player Score</p>
            <div>
              <vue3-star-ratings
                v-model="playerScore"
                :showControl="false"
                disableClick
              />
            </div>
          </div>
          <div class="column">
            <p>Computer Score</p>
            <div>
              <vue3-star-ratings
                v-model="computerScore"
                :showControl="false"
                disableClick
              />
            </div>
          </div>
        </div>

        <p>What do you chose?</p>
        <MDBRow class="align-items-start">
          <MDBCol>
            <button class="button is-ghost" @click="optionChoice(0)">
              <b>ROCK</b>
            </button>
            <img
              src="https://shinigamidevelopment.000webhostapp.com/img/rock.svg"
              style="width: 400px; height: 400px"
            />
          </MDBCol>
          <MDBCol>
            <button class="button is-ghost" @click="optionChoice(1)">
              <b>PAPER</b>
            </button>
            <img
              src="https://shinigamidevelopment.000webhostapp.com/img/paper.svg"
              style="width: 400px; height: 400px"
          /></MDBCol>
          <MDBCol>
            <button class="button is-ghost" @click="optionChoice(2)">
              <b>SCISSORS</b>
            </button>
            <img
              src="https://shinigamidevelopment.000webhostapp.com/img/scissors.svg"
              style="width: 400px; height: 400px"
          /></MDBCol>
        </MDBRow>
      </div>
    </MDBContainer>
    <Toaster />
  </div>
</template>

<style lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Cabin&display=swap");
body {
  height: 1200px;
  background-color: #cff3ff;
}
#api-post {
  background-color: #cff3ff;
  font-family: "Cabin";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #174e60;
}
</style>

<script lang="ts">
import { defineComponent } from "vue";
import "boxicons";
import {
  MDBModal,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
} from "mdb-vue-ui-kit";
import vue3StarRatings from "vue3-star-ratings";
import Toaster from "@meforma/vue-toaster";

export default defineComponent({
  components: {
    MDBModal,
    MDBModalHeader,
    MDBModalTitle,
    MDBModalBody,
    MDBModalFooter,
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol,
    vue3StarRatings,
    Toaster,
  },
  data() {
    return {
      isActive: false,
      isGameActive: false,
      playerScore: 0,
      computerScore: 0,
      winCount: 5,
    };
  },
  methods: {
    startFunction() {
      this.isActive = true;
    },
    startGame() {
      this.isActive = !this.isActive;
      this.isGameActive = !this.isGameActive;
    },
    getRandomInt(min: number, max: number) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
    },
    determineWin(player: number, computer: number) {
      switch (player) {
        case 0:
          if (computer === 0) {
            Toaster.info(`TIE ROUND!`);
          } else {
            if (computer === 1) {
              Toaster.error(`ROUND LOST!`);
              this.computerScore = this.computerScore + 1;
            } else {
              if (computer === 2) {
                Toaster.success(`ROUND WIN!`);
                this.playerScore = this.playerScore + 1;
              }
            }
          }
          break;
        case 1:
          if (computer === 0) {
            Toaster.success(`ROUND WIN!`);
            this.playerScore = this.playerScore + 1;
          } else {
            if (computer === 1) {
              Toaster.info(`TIE ROUND!`);
            } else {
              if (computer === 2) {
                Toaster.error(`ROUND LOST!`);
                this.computerScore = this.computerScore + 1;
              }
            }
          }
          break;
        case 2:
          if (computer === 0) {
            Toaster.error(`ROUND LOST!`);
            this.computerScore = this.computerScore + 1;
          } else {
            if (computer === 1) {
              Toaster.success(`ROUND WIN!`);
              this.playerScore = this.playerScore + 1;
            } else {
              if (computer === 2) {
                Toaster.info(`TIE ROUND!`);
              }
            }
          }
          break;
      }
    },
    optionChoice(e: number) {
      const compChoice = this.getRandomInt(0, 2);
      switch (e) {
        case 0:
          //ROCK
          this.determineWin(0, compChoice);
          break;
        case 1:
          //PAPER
          this.determineWin(1, compChoice);
          break;
        case 2:
          //SCISSORS
          this.determineWin(2, compChoice);
          break;
      }
    },
  },
  mounted() {
    setTimeout(() => {
      this.startFunction();
    }, 2000);
  },
});
</script>
