
import { defineComponent } from "vue";
import "boxicons";
import {
  MDBModal,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
} from "mdb-vue-ui-kit";
import vue3StarRatings from "vue3-star-ratings";
import Toaster from "@meforma/vue-toaster";

export default defineComponent({
  components: {
    MDBModal,
    MDBModalHeader,
    MDBModalTitle,
    MDBModalBody,
    MDBModalFooter,
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol,
    vue3StarRatings,
    Toaster,
  },
  data() {
    return {
      isActive: false,
      isGameActive: false,
      playerScore: 0,
      computerScore: 0,
      winCount: 5,
    };
  },
  methods: {
    startFunction() {
      this.isActive = true;
    },
    startGame() {
      this.isActive = !this.isActive;
      this.isGameActive = !this.isGameActive;
    },
    getRandomInt(min: number, max: number) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min) + min); //The maximum is exclusive and the minimum is inclusive
    },
    determineWin(player: number, computer: number) {
      switch (player) {
        case 0:
          if (computer === 0) {
            Toaster.info(`TIE ROUND!`);
          } else {
            if (computer === 1) {
              Toaster.error(`ROUND LOST!`);
              this.computerScore = this.computerScore + 1;
            } else {
              if (computer === 2) {
                Toaster.success(`ROUND WIN!`);
                this.playerScore = this.playerScore + 1;
              }
            }
          }
          break;
        case 1:
          if (computer === 0) {
            Toaster.success(`ROUND WIN!`);
            this.playerScore = this.playerScore + 1;
          } else {
            if (computer === 1) {
              Toaster.info(`TIE ROUND!`);
            } else {
              if (computer === 2) {
                Toaster.error(`ROUND LOST!`);
                this.computerScore = this.computerScore + 1;
              }
            }
          }
          break;
        case 2:
          if (computer === 0) {
            Toaster.error(`ROUND LOST!`);
            this.computerScore = this.computerScore + 1;
          } else {
            if (computer === 1) {
              Toaster.success(`ROUND WIN!`);
              this.playerScore = this.playerScore + 1;
            } else {
              if (computer === 2) {
                Toaster.info(`TIE ROUND!`);
              }
            }
          }
          break;
      }
    },
    optionChoice(e: number) {
      const compChoice = this.getRandomInt(0, 2);
      switch (e) {
        case 0:
          //ROCK
          this.determineWin(0, compChoice);
          break;
        case 1:
          //PAPER
          this.determineWin(1, compChoice);
          break;
        case 2:
          //SCISSORS
          this.determineWin(2, compChoice);
          break;
      }
    },
  },
  mounted() {
    setTimeout(() => {
      this.startFunction();
    }, 2000);
  },
});
